import _ from "lodash";
import { ActionTypes } from "../../Constants";
import { dispatcher } from "../../Dispatcher";
import RealtimeDatabase from "../../RealtimeDatabase";
import Store from "../../Store";
import { GroupDm } from "../Group";
import GroupStore from "./GroupStore";
import UserStore from "./UserStore";

const cache = {};
const cleanupCache = {};

const TypingStoreClass = class TypingStore extends Store {
	_getRefPath(circleId, groupId = null) {
		return circleId === "dms"
			? `typingIndicators/dms/${GroupDm.getId(groupId)}`
			: groupId
				? `typingIndicators/${circleId}/${groupId}`
				: `typingIndicators/${circleId}`;
	}

	getTypingUsersForCircle(circleId) {
		if (circleId === "dms") {
			throw new Error("Not allowed to fetch all DM typing indicators!");
		}

		return _.get(cache, [circleId]);
	}

	getTypingUsersForGroup(circleId, groupId) {
		return _.get(cache, [circleId, groupId], {});
	}

	getAllTypingUsers() {
		return cache;
	}

	tryCreateListenerFor(circleId, userId = null) {
		if (cleanupCache[userId ?? circleId] || !circleId && !userId) {
			return;
		}

		const typingRefPath = this._getRefPath(circleId, userId);

		const disconnectListener = RealtimeDatabase.createDisconnectListener(
			`${typingRefPath}/${UserStore.getLocalUser().id}`
		);

		disconnectListener.remove();

		const unbind = RealtimeDatabase.createListener(
			typingRefPath,
			snapshot => {
				const data = snapshot.val();

				dispatcher.dispatch({
					type: ActionTypes.UPDATE_TYPING_INDICATORS,
					indicators: data,
					circleId, userId
				});
			}
		);

		cleanupCache[userId ?? circleId] = () => {
			unbind();
			disconnectListener.cancel();

			delete cleanupCache[userId ?? circleId];
		};
	}

	startTyping() {
		const group = GroupStore.getSelectedGroup();
		if (!group) return;

		const path = `${this._getRefPath(group.circleId, group.id)}/${UserStore.getLocalUser().id}`;

		if (!_.get(this.getTypingUsersForGroup(group.circleId, group.id), [UserStore.getLocalUser().id])) {
			RealtimeDatabase.set(path, { t: true }).catch(console.error.bind(null, "Failed to set typing state!"));
		}

		this.stopTypingSoft(group.circleId, group.id);
	}

	stopTyping(circleId, groupId) {
		if (!circleId) {
			const group = GroupStore.getSelectedGroup();
			if (!group) return;

			circleId = group.circleId;
			groupId = group.id;
		}

		const path = `${this._getRefPath(circleId, groupId)}/${UserStore.getLocalUser().id}`;

		RealtimeDatabase.set(path, null).catch(console.error.bind(null, "Failed to stop typing!"));
	}

	stopTypingSoft = _.debounce(this.stopTyping.bind(this), 5000);
}

const TypingStore = new TypingStoreClass(dispatcher, {
	[ActionTypes.UPDATE_TYPING_INDICATORS]: ({ circleId, userId, indicators }) => {
		_.set(cache, [userId || circleId], indicators);
	}
});

export default TypingStore;