import React, { useEffect } from "react";
import "./city-fog-theme.css";
import "./App.scss";
import { Modals } from "./Components/Modals";
import HomePage from "./Pages/Home";
import Toasts from "./Components/Toasts";
import { ActionTypes, joinClassNames } from "./Classes/Constants";
import ContextMenu from "./Components/ContextMenuHandler";
import PageFooter from "./Components/PageElements/PageFooter";
import { dispatcher } from "./Classes/Dispatcher";
import { onAuthStateChanged } from "@firebase/auth";
import Database from "./Classes/Database";
import AppPage from "./Pages/AppPage";
import UserStore from "./Classes/Types/Stores/UserStore";
import RealtimeDatabase from "./Classes/RealtimeDatabase";
import BannerNotifications from "./Components/PageElements/BannerNotifications";

window.addEventListener("popstate", () => {
	dispatcher.dispatch({
		type: ActionTypes.PAGE_UPDATE
	});
});

// Because of GH pages, handle formatting the URL if on pages.
function formatUrl(path) {
	return path.startsWith("#") ? path : `#${path}`;
}

export function pushHistoryState(path) {
	path = formatUrl(path);

	window.history.pushState({ last: window.location.hash, lastTime: Date.now() }, null, path);

	dispatcher.dispatch({
		type: ActionTypes.PAGE_UPDATE
	});
}

export function replaceHistoryState(path) {
	path = formatUrl(path);

	window.history.replaceState({ last: window.location.hash, lastTime: Date.now() }, null, path);

	dispatcher.dispatch({
		type: ActionTypes.PAGE_UPDATE
	});
}

export let appHash = "";
export let appHashArgs = [];

function PageElement() {
	// Use the force updater from the page update dispatch.
	dispatcher.useForceUpdater(ActionTypes.PAGE_UPDATE);

	// Store the formatted hash in a variable.
	const [hash, ...args] = (window.location.hash
		.split(/#\/?/)[1] ?? "")
		.split("?")[0]
		.split("/");

	// Set the app hash value.
	appHash = hash;
	appHashArgs = args;

	// Zhu Li, do the thing!
	switch (hash) {
		default: return <HomePage />;
		case "app": return <AppPage />;
	}
}

export default function App() {
	dispatcher.useForceUpdater(ActionTypes.PAGE_UPDATE);

	React.useEffect(() => {
		// Check if the user is signed in.
		onAuthStateChanged(Database.auth, async localUser => {
			let user = null;

			if (localUser?.uid) {
				user = await UserStore.fetchUser(localUser.uid);

				// Create a disconnect listener to set the status to offline
				const offlineListener = RealtimeDatabase.createDisconnectListener(`userstatus/${user.id}`);
				await offlineListener.setWithPriority({ o: false }, 100);

				// Set the status to online
				RealtimeDatabase.set(`userstatus/${user.id}`, { o: true });
			}

			dispatcher.dispatch({
				type: ActionTypes.LOCAL_USER_UPDATE,
				user
			});
		});

		if (~navigator.userAgent.indexOf("Firefox")) {
			dispatcher.dispatch({
				type: ActionTypes.DISPLAY_BANNER_NOTIFICATION,
				notification: () => (
					<>
						<b>WARNING</b> - You're using an unsupported browser. You <b>will</b> run into bugs and issues; please do not report these without testing on a modern web browser beforehand.
					</>
				)
			});
		}
	}, []);

	return (
		<div className="App">
			<BannerNotifications />

			<div className="Main">
				<PageElement />
			</div>

			{window.location.hash.split("/")[0].split("?")[0] !== "#app" && <PageFooter />}

			<Modals />
			<Toasts />
			<ContextMenu.Handler />
		</div>
	);
}