import React from "react";
import { X } from "react-feather";
import { ActionTypes } from "../../Classes/Constants";
import { dispatcher } from "../../Classes/Dispatcher";
import "./BannerNotifications.scss";

export default function BannerNotifications() {
	const [notifications, setNotifications] = React.useState([]);

	React.useEffect(() => {
		const callback = ({ notification }) => {
			setNotifications([...notifications, notification]);
		};

		dispatcher.subscribe(ActionTypes.DISPLAY_BANNER_NOTIFICATION, callback);
		return () => dispatcher.unsubscribe(ActionTypes.DISPLAY_BANNER_NOTIFICATION, callback);
	}, [notifications]);

	return (
		<div className="BannerNotifications">
			{!!notifications.length && (
				<div className="Notif FlexCenter">
					<div className="Contents">
						{React.createElement(notifications[0] ?? "")}
					</div>

					<X
						className="Button CloseButton"
						onClick={() => setNotifications(notifications.slice(1))}
					/>
				</div>
			)}
		</div>
	);
}