import { limitToLast, orderBy, query } from "@firebase/firestore";
import _ from "lodash";
import { ActionTypes } from "../../Constants";
import Database from "../../Database";
import { dispatcher } from "../../Dispatcher";
import Store from "../../Store";
import { GroupDm } from "../Group";
import Message from "../Message";
import MessageCountStore from "./MessageCountStore";

const cache = {};
const cleanupCache = {};
const fetching = {};

export const MessageStoreClass = class MessageStore extends Store {
	getMessage(circleId, groupId, messageId) {
		return _.get(cache, [circleId, groupId, messageId]);
	}

	getMessages(circleId, groupId) {
		return _.get(cache, [circleId, groupId]) ?? (
			this.fetchMessages(circleId, groupId),
			null
		);
	}

	getMessageGroups(circleId, groupId) {
		// TODO add unread divider

		const _messages = this.getMessages(circleId, groupId);
		if (!_messages) return null;

		const messages = Object.values(_messages);
		const groups = [];

		let lastMessage;
		let currentGroup;

		for (const message of messages) {
			const isSeparateUser = lastMessage?.senderId !== message.senderId;
			const isSeparateTime = message.creationDate - lastMessage?.creationDate > 600;

			if (!lastMessage || isSeparateUser || isSeparateTime) {
				currentGroup && lastMessage && groups.push({
					key: "mg-" + lastMessage.id,
					userId: lastMessage.senderId,
					messages: currentGroup
				});

				currentGroup = [];
			}

			currentGroup.push(message);
			lastMessage = message;
		}

		currentGroup && groups.push({
			key: "mg-" + currentGroup[0].id,
			userId: currentGroup[0].senderId,
			messages: currentGroup
		});

		return groups.reverse();
	}

	getAllCachedMessages() {
		return cache;
	}

	getCleanupCache() {
		return cleanupCache;
	}

	tickCache(circleId, groupId) {
		const cleanup = _.get(cleanupCache, [circleId, groupId], {});

		cleanup.tick?.();
	}

	cleanup(circleId, groupId) {
		const cleanup = _.get(cleanupCache, [circleId, groupId], {});

		cleanup.instantly?.();
	}

	async fetchMessages(circleId, groupId) {
		if (_.get(fetching, [circleId, groupId])) return;
		_.set(fetching, [circleId, groupId], true);

		const dmId = circleId === "dms"
			? GroupDm.getId(groupId)
			: null;

		return new Promise(r => {
			const ref = circleId === "dms"
				? Database.collection(
					"directmessages",
					dmId,
					"messages"
				)
				: Database.collection(
					"circles",
					circleId,
					"groups",
					groupId,
					"messages"
				);

			const unbind = Database.createSnapshotListener(
				query(
					ref,
					orderBy("creationDate"),
					limitToLast(50)
				),
				({ changes = [] } = {}) => {
					_.set(fetching, [circleId, groupId], null);

					for (const change of changes) {
						switch (change.type) {
							case "added": {
								dispatcher.dispatch({
									type: ActionTypes.MESSAGE_CREATE,
									circleId, groupId,
									message: new Message(change.data)
								});

								if (changes.length <= 2) {
									MessageCountStore.pushValue(circleId, groupId);
								}
							} break;

							case "modified": {
								dispatcher.dispatch({
									type: ActionTypes.MESSAGE_UPDATE,
									circleId, groupId,
									message: _.extend(
										this.getMessage(circleId, groupId)
										?? new Message(change.data),
										change.data
									)
								});
							} break;

							case "removed": {
								dispatcher.dispatch({
									type: ActionTypes.MESSAGE_DELETE,
									circleId, groupId,
									messageId: change.doc.id
								});
							} break;
						}
					}

					setImmediate(() => {
						dispatcher.dispatch({
							type: ActionTypes.CHAT_UPDATE,
							circleId, groupId
						});
					});

					r(_.get(cache, [circleId, groupId]));
				}
			);

			const readRef = circleId === "dms"
				? Database.collection(
					"directmessages",
					dmId,
					"lastread"
				)
				: Database.collection(
					"circles",
					circleId,
					"groups",
					groupId,
					"lastread"
				);

			const unbindReadListener = Database.createSnapshotListener(
				query(
					readRef,
					orderBy("timestamp"),
					limitToLast(10)
				),
				({ items = [] } = {}) => {
					dispatcher.dispatch({
						type: ActionTypes.UPDATE_READS,
						circleId,
						groupId,
						reads: items
					});
				}
			);

			const cleanup = () => {
				unbind();
				unbindReadListener();

				delete cache[circleId][groupId];
				delete cleanupCache[circleId][groupId];
			};

			_.set(cleanupCache, [circleId, groupId], {
				instantly: cleanup,
				tick: _.debounce(cleanup, 10 * 60 * 1000)
			});
		});
	}
}

const MessageStore = new MessageStoreClass(dispatcher, {
	[ActionTypes.MESSAGE_CREATE]: ({ circleId, groupId, message }) => {
		_.set(cache, [circleId, groupId, message.id], message);
	},
	[ActionTypes.MESSAGE_UPDATE]: ({ circleId, groupId, message }) => {
		_.set(cache, [circleId, groupId, message.id], message);
	},
	[ActionTypes.MESSAGE_DELETE]: ({ circleId, groupId, messageId }) => {
		delete cache[circleId]?.[groupId]?.[messageId];
	}
})

export default MessageStore;