import _ from "lodash";
import React from "react";
import { Check, User } from "react-feather";
import { ActionTypes, formatTimestamp, joinClassNames } from "../../Classes/Constants";
import { dispatcher } from "../../Classes/Dispatcher";
import { useInterval, useMediaQuery } from "../../Classes/Hooks";
import CssStore from "../../Classes/Types/Stores/CssStore";
import GroupStore from "../../Classes/Types/Stores/GroupStore";
import MessageReadStore from "../../Classes/Types/Stores/MessageReadStore";
import MessageStore from "../../Classes/Types/Stores/MessageStore";
import UserStore from "../../Classes/Types/Stores/UserStore";
import { UserAvatarComponent, UserMention } from "../../Classes/Types/User";
import ContextMenu from "../ContextMenuHandler";
import InlineLoading from "../InlineLoading";
import Tooltip from "../Tooltip";
import "./ChatPanel.scss";

export default function ChatPanel({ circleId, groupId }) {
	dispatcher.useForceUpdater(
		ActionTypes.CHAT_UPDATE,
		e => e.circleId == circleId && e.groupId == groupId
	);

	const ref = React.useRef();

	const messages = MessageStore.getMessageGroups(circleId, groupId);
	const isMobile = useMediaQuery("max-width", 700);

	const mobileEvents = React.useMemo(() => isMobile ? {
		onTouchStart: e => {
			const tapX = e.touches[0].clientX;
			const tapY = e.touches[0].clientY;
			const target = CssStore.getStyle("groupPanelSize") + 60;

			let translation = 0;
			let lastTranslation = parseFloat(ref.current.style.transform.match(/[0-9]+/g));
			if (isNaN(lastTranslation)) lastTranslation = 0;

			const handler = e => {
				const alias = 50;
				translation = e.touches[0].clientX - tapX;

				if (Math.abs(translation) < alias || Math.abs(e.touches[0].clientY - tapY) > 50) return;
				if (translation < 0) translation += alias;
				else translation -= alias;

				Object.assign(ref.current.style, {
					transform: `translateX(${_.clamp(lastTranslation + translation, 0, target)}px)`
				});
			};

			document.addEventListener("touchmove", handler);

			let remove;
			document.addEventListener("touchend", remove = () => {
				setTimeout(() => {
					Object.assign(ref.current.style, {
						transform: translation > target / 3 ? `translateX(${target}px)` : "none"
					});

					document.removeEventListener("touchmove", handler);
					document.removeEventListener("touchend", remove);
				}, 100);
			});
		}
	} : {}, [isMobile]);

	useInterval(
		() => MessageStore.tickCache(circleId, groupId),
		1000,
		true,
		[circleId, groupId]
	);

	return (
		<div
			className="Panel PanelChat"
			ref={ref}
			{...mobileEvents}
		>
			{messages
				? (
					<div className="Messages">
						{messages.map(group => (
							<MessageGroup {...group} />
						))}
					</div>
				)
				: (
					<div
						className="FlexCenter"
						style={{
							marginTop: 50
						}}
					>
						<InlineLoading />
					</div>
				)}
		</div>
	);
}

export function MessageGroup({ userId, messages }) {
	const user = UserStore.useState(() => UserStore.getUser(userId));

	return (
		<div className={
			joinClassNames(
				"MessageGroup",
				[userId === UserStore.getLocalUser()?.id, "LocalUser"]
			)}
		>
			<ContextMenu.Wrapper
				menu={<user.ContextMenu />}
				wrapperClassName="UserSection Flex"
			>
				{user && <user.AvatarComponent />}

				<div className="Details">
					<h3 className="Username">{user.displayName}</h3>

					<user.StatusText />
				</div>
			</ContextMenu.Wrapper>

			{messages.map(message => (
				<MessageComponent message={message} key={message.id} />
			))}
		</div>
	);
}

function MessageComponent({ message }) {
	const group = GroupStore.getSelectedGroup();

	dispatcher.useForceUpdater(
		ActionTypes.UPDATE_READS,
		({ circleId, groupId }) =>
			group?.circleId === circleId && group?.id === groupId
	);

	const reads = MessageReadStore.getReadsForMessage(
		group?.circleId,
		group?.id,
		message.id
	).filter(({ userId }) => userId !== UserStore.getLocalUser().id);

	return (
		<div className="Message Flex" key={message.id}>
			<div className="TimestampContainer">
				<span className="Timestamp">
					{message.creationDate?.toDate().toLocaleTimeString("en-US", { hour12: false }).slice(0, -3)}
				</span>

				<Tooltip direction="right">
					<div>Sent {message.getCreationDate()}</div>

					{message.editedAt && (
						<div>Edited {message.getCreationDate(message.editedAt)}</div>
					)}
				</Tooltip>
			</div>

			<div className="MessageContent">
				{message.getContent()}
			</div>

			{!!reads.length && (
				<div className="ReadIndicators">
					<Check className="Icon" />

					<Tooltip direction="left">
						<h4>Read by</h4>

						<div className="Flex Indicators">
							{reads.map(({ userId, timestamp }) => (
								<div key={userId}>
									<UserMention user={UserStore.getUser(userId)} /> {formatTimestamp(timestamp)}
								</div>
							))}
						</div>
					</Tooltip>
				</div>
			)}
		</div>
	);
}