import { get, getDatabase, onDisconnect, onValue, ref, set } from "firebase/database";
import _ from "lodash";

export default new class RealtimeDatabase {
	db = getDatabase();

	serializeObject(obj) {
		return _.omit({ ...obj }, [
			"_ref",
			"_unbindListener",
			"id"
		]);
	}

	ref(path) {
		return ref(this.db, path);
	}

	createListener(path, callback) {
		return onValue(
			this.ref(path),
			callback
		);
	}

	createDisconnectListener(path) {
		return onDisconnect(this.ref(path));
	}

	async get(path) {
		return await get(this.ref(path));
	}

	async set(path, data) {
		return await set(this.ref(path), data ? this.serializeObject(data) : null);
	}
}