import React from "react";
import { Film, MessageSquare, Send } from "react-feather";
import { ActionTypes, joinClassNames } from "../../Classes/Constants";
import { dispatcher } from "../../Classes/Dispatcher";
import GroupStore from "../../Classes/Types/Stores/GroupStore";
import TypingStore from "../../Classes/Types/Stores/TypingStore";
import UserStore from "../../Classes/Types/Stores/UserStore";
import { UserMention } from "../../Classes/Types/User";
import Toasts from "../Toasts";
import "./ChatTextArea.scss";
import RichTextArea from "./RichTextArea";

export default function ChatTextArea() {
	const events = {
		handleSend: async message => {
			if (message.trim()) {
				const group = GroupStore.getSelectedGroup();
				if (!group) return Toasts.showToast("No selected group!", "Failure");

				group.sendMessage(message);
				TypingStore.stopTyping(group.circleId, group.id);
			}

			return true;
		}
	};

	return (
		<div className="ChatTextAreaContainer Flex">
			<RichTextArea
				id="ChatTextArea"
				className="ChatTextArea"
				onSend={events.handleSend}
				onInput={e => e.target.value ? TypingStore.startTyping() : TypingStore.stopTyping()}
				containerChildren={<TypingIndicators />}
			><MessageSquare /> Say hi! </RichTextArea>

			<div className="Button">
				<Film />
			</div>

			<div className="Button" onClick={() => (
				document.getElementById("ChatTextArea").dispatchEvent(
					new KeyboardEvent("keydown", {
						key: "Enter",
						shiftKey: false,
						bubbles: true
					})
				)
			)}>
				<Send />
			</div>
		</div>
	)
}

function TypingIndicators() {
	dispatcher.useForceUpdater(ActionTypes.PAGE_UPDATE);
	dispatcher.useForceUpdater(
		ActionTypes.UPDATE_TYPING_INDICATORS,
		({ circleId, userId }) =>
			[circleId, userId].some(id => window.location.href.includes(id))
	);

	const group = GroupStore.getSelectedGroup() ?? {};

	const typingUserIds = Object.keys(
		TypingStore.getTypingUsersForGroup(group.circleId, group.id) ?? {}
	);

	return (
		<div
			className={
				joinClassNames(
					"TypingIndicators",
					[!!typingUserIds.length, "Active"]
				)
			}
		>
			{typingUserIds.map((userId, i) => (
				<React.Fragment key={userId}>
					<UserMention user={UserStore.getUser(userId)} convertToYou />

					{i !== typingUserIds.length - 1 && (
						<div className="Comma">
							, {i === typingUserIds.length - 2 && "and"}
						</div>
					)}
				</React.Fragment>
			))}

			{typingUserIds.length > 1 || ~typingUserIds.indexOf(UserStore.getLocalUser().id)
				? " are"
				: " is"} typing...
		</div>
	);
}