import _ from "lodash";
import { ActionTypes } from "../../Constants";
import { dispatcher } from "../../Dispatcher";
import Store from "../../Store";
import UserStore from "./UserStore";

const cache = {};

const MessageReadStoreClass = class MessageReadStore extends Store {
	getReads(circleId, groupId) {
		return _.get(cache, [circleId, groupId]);
	}

	getReadsForMessage(circleId, groupId, messageId) {
		const reads = this.getReads(circleId, groupId) ?? {};

		return Object.entries(reads)
			.filter(([, read]) => read.messageId === messageId)
			.map(([userId, read]) => ({
				userId,
				timestamp: read.timestamp
			}));
	}

	getAllCachedReads() {
		return cache;
	}
}

const MessageReadStore = new MessageReadStoreClass(dispatcher, {
	[ActionTypes.UPDATE_READS]: ({ circleId, groupId, reads }) => {
		// Assign it so we know that read counts have been loaded
		if (!_.get(cache, [circleId, groupId])) {
			_.set(cache, [circleId, groupId], {});
		}

		// And assign each read count
		for (const read of reads) {
			cache[circleId][groupId][read.id] =
				_.pick(read, "messageId", "timestamp");
		}
	}
});

export default MessageReadStore;