import _ from "lodash";
import { formatDateString, formatTimestamp } from "../Constants";
import MessageFormatter from "../MessageFormatter";

export default class Message {
	id = "";
	content = "";
	senderId = "";
	creationDate = null;
	mentions = [];

	constructor(message) {
		_.extend(this, message);
	}

	getContent(props = {}) {
		return MessageFormatter.parse(this.content, this, props);
	}

	getCreationDate(_time) {
		return formatTimestamp(_time ?? this.creationDate);
	}
}